<template>
  <div class="with-draw">
    <nav-head title="提现" FixedOrTransparency="isFixed NoTransparency"/>
    <div class="item-wrap" style="margin-top: 0;">
      <div class="item">
        <span>真实姓名</span>
        <input type="text" v-model="UserBankInfo.user_name" disabled/>
      </div>
      <div class="item">
        <span>银行名称</span>
        <input type="text" v-model="UserBankInfo.bank_name" disabled/>
      </div>
      <div class="item">
        <span>卡号</span>
        <input type="text" maxlength="25" v-model="UserBankInfo.bank_num" disabled/>
      </div>
    </div>
    <div class="item-wrap">
      <div class="item">
        <span>提现密码</span>
        <input type="password" maxlength="6" v-model="password" @input="handleInputNum('password',$event)" placeholder="请输入提现密码" v-bind:class="{'bank': password }"/>
        <span @click="clearValue('password')" class="icon-del-365" v-bind:class="{'show': password }"></span>
      </div>
    </div>
    <div class="item-wrap border-bottom">
      <div class="item">
        <span>提现金额</span>
        <input type="text" maxlength="9" v-model="money" @input="handleInputNum('money',$event)" placeholder="请输入提现金额" v-bind:class="{'bank': money }"/>
        <span @click="clearValue('money')" class="icon-del-365" v-bind:class="{'show': money }"></span>
      </div>
    </div>
    <div class="triangle-bg"></div><div class="triangle"></div>
    <div class="limit-wrap">可提现金额<span>{{UserBankInfo.diamond||0}}</span></div>
    <div class="padding-0-15">
      <mt-button size="large" v-bind:disabled="!money || !password" @click.native="bindBank" class="btn-bind" v-bind:class="{'available': money && password }">确认提现</mt-button>
      <p class="text">注意：投注额必须大于或等于充值额，才能进行提现。有任何疑问，请您联系我们的客服。</p>
    </div>
  </div>
</template>
<script>//
import NavHead from '@/components/NavHead.vue'
import { Button, Toast } from 'mint-ui'
import { mapActions, mapState } from 'vuex'
import { GET_USER_INFO, WITH_DRAW } from '../../../vuex/Mine'

export default {
  name: 'withDraw',
  data () {
    return {
      money: '',
      password: ''
    }
  },
  methods: {
    bindBank () {
      if (!this.password || this.password.length !== 6) {
        Toast('请输入正确的提现密码!')
        return
      }
      if (!this.money) {
        Toast('请输入正确的提现金额!')
        return
      }
      this.withDraw({
        money: this.money,
        password: this.password
      }).then(data => {
        if (data) {
          Toast('提现成功')
        }
        return this.getUserInfo()
      }).then(() => {
        this.$router.back()
      }).catch(() => {
        console.log('失败')
      })
    },
    clearValue (v) {
      this[v] = ''
    },
    handleInputNum (v, e) {
      this[v] = e.target.value.replace(/[^\d]/g, '')
    },
    ...mapActions({
      withDraw: WITH_DRAW,
      getUserInfo: GET_USER_INFO
    })
  },
  components: {
    NavHead,
    [Button.name]: Button
  },
  computed: {
    ...mapState({
      UserInfo: state => state.mine.User,
      UserBankInfo: state => state.mine.UserBankInfo
    })
  }
}
</script>
<style scoped lang="scss">
  div.with-draw {
    position: relative;
    > div.item-wrap {
      background-color: $background-light-color;
      padding: 0 15px;
      margin-top: 18px;
      .item {
        height: 54px;
        line-height: 54px;
        text-align: left;
        font-size: 17px;
        position: relative;
        overflow: hidden;
        border-bottom: 1px solid $border-color;
        span:first-child {
          color: $font-color;
        }
        input {
          float: right;
          border: none;
          outline: none;
          display: inline-block;
          width: 75%;
          position: absolute;
          right: 0;
          top: 0;
          padding: 0 0;
          font-size: 15px;
          height: 54px;
          line-height: 54px;
          color: #CCC;
          background: transparent;
          border-radius: 0;
          text-align: right;
        }
        input.bank {
          margin-right: 30px;
        }
        .icon-del-365 {
          position: absolute;
          right: 0;
          top: 15px;
          display: none;
        }
        .icon-del-365.show {
          display: block;
        }
      }
      .item:last-child {
        border-bottom: none;
      }
    }
    > div.limit-wrap {
      position: relative;
      z-index: 2;
      background-color: $background-light-color;
      color: #CCC;
      font-size: 15px;
      height: 54px;
      line-height: 54px;
      padding: 0 15px;
      span {
        padding-left: 15px;
      }
    }
    .btn-bind {
      border: none;
      background-color: #ccc;
      color: white;
      box-shadow: none;
      height: 48px;
      font-size: 17px;
      margin-top: 40px;
    }
    .btn-bind.available{
      background-color: $back-color;
      @if $IF365 { color: #131313; }
    }
    > div.triangle-bg {
      width:0;
      height:0;
      border-width:0 15px 15px;
      border-style: solid;
      @if $IF365 { border-color: transparent transparent #262626; }
      @else { border-color: transparent transparent #eee; }
      margin: -12px 0 0 27px;
      position: absolute;
    }
    > div.triangle {
      width:0;
      height:0;
      border-width:0 12px 12px;
      border-style: solid;
      border-color: transparent transparent $background-light-color;
      margin: -11px 0 0 30px;
      position: absolute;
    }
  }
  .padding-0-15 {
    padding: 0 15px;
  }
  .text {
    color: #CCC;
    font-size: 12px;
    padding-top: 15px;
    line-height: 20px;
    padding-bottom: 20px;
  }
  .border-bottom {
    @if $IF365 { border-bottom: 1px solid #262626; }
    @else { border-bottom: 1px solid #eee; }
  }
</style>
